@media print {
  .dp-po .no-print {
    display: none; }
  .dp-po .dp-po-header .dp-po-header-right {
    display: none; }
  .dp-po .breadcrumb {
    justify-content: flex-start; }
  .dp-po .breadcrumb-outer {
    margin: -5px 14px 0;
    border-top: 2px solid #D3D6D7; }
  .dp-po .dp-po-search {
    display: none; }
  .dp-po .db-po-post-rating .btn {
    display: none; }
  .dp-po .dp-po-main {
    padding: 20px 0;
    background-color: #fff; }
  .dp-po .dp-po-post-avatars a {
    text-decoration: none; }
  .dp-po .dp-po-subscribe-btn {
    display: none; }
  .dp-po .dp-po-post-extra {
    display: none; }
  .dp-po .dp-po-post-content {
    border: 1px solid #D3D6D7;
    border-radius: 5px; }
  .dp-po a.badge {
    text-decoration: none; }
  .dp-po .dp-po-comments > .dp-po-block {
    border: 1px solid #D3D6D7;
    border-radius: 0 0 4px 4px; }
  .dp-po .dp-po-footer {
    background-color: #fff; }
  .sf-toolbar {
    display: none !important; }
  body {
    padding: 20px !important; } }

/*
Styles which are out of .dp-po-* scope
e.g. Modal Backdrop
*/
.modal-backdrop {
  background: #4C4F50;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh; }
  .modal-backdrop.show {
    opacity: 0.5; }

body {
  margin: 0;
  padding: 0;
  font-size: 1.125rem;
  color: var(--text-color); }
  body.mobile-modal-open {
    overflow: hidden; }
